      <!-- TODO: 因暂时没有接口数据, 所以先将图片地址写死,后期开放 -->
      <!-- <Logo
        class="logo-style"
        :imgUrl="unionDetails.logo"
        :size="250"
        round
        border
      /> -->
      <Logo class="logo-style" :size="250" round border />

      <Card :radius="16" class="introduction-box">
        <p class="introduction-title">{{ unionDetails.name }}</p>
        <p class="introduction-content">{{ unionDetails.info }}</p>
      </Card>

      <van-tabs class="tabs-style" ref="tabs" v-model="active" color="#0077FF">
        <van-tab title="成员名单">
          <!-- TODO: 咱无图片数据,所以先用组件列表替代 -->
          <!-- <van-image
            class="members-the-list-img"
            :src="unionDetails.poster"
          ></van-image> -->

          <ul class="tab-content">
            <li
              class="tab-item"
              v-for="(item, index) in memberList"
              :key="index"
            >
              <MembersList :title="item.label" :list="item.list" />
            </li>
          </ul>
        </van-tab>
        <van-tab title="讲师公示">
          <div class="tab-content">
            <van-search
              class="search-style"
              v-model="keyword"
              placeholder="请输入讲师名称"
              shape="round"
            />

            <p class="main-talker-box-label">全部主讲人(5)</p>

            <ul class="main-talker-list">
              <li
                v-for="(item, index) in mainTalkerList"
                :key="index"
                class="main-talker-item"
              >
                <MainTalker
                  :avatarUrl="item.img"
                  :userName="item.name"
                  :patrolDistricts="item.area_count"
                  :activitySession="item.lecture_count"
                  :participation="item.sum_visitor"
                />
              </li>
            </ul>
          </div>
        </van-tab>
      </van-tabs>
    </article>
  </div>
</template>

