    }
    .introduction-box {
      padding: px(37) px(49) px(40);
      .introduction-title {
        width: auto;
        font-size: px(48);
        font-weight: 800;
        color: #000000;
        text-decoration: underline solid $primary-color px(12);
      }
      .introduction-content {
        margin-top: px(23);
        font-size: px(26);
        color: #000000;
        line-height: px(48);
      }
    }
    .tabs-style {
      margin-top: px(40);
      ::v-deep .van-tabs__nav {
        background-color: transparent;
        .van-tab {
          font-size: px(36);
